<template>
  <view class="notice-bar-box">
    <!-- <uni-notice-bar
      v-if="noticeList.length > 0"
      show-get-more
      show-icon
      :color="themeConfig['hubei-primary']"
      :more-color="themeConfig['hubei-primary']"
      :background-color="themeConfig['hubei-primary-light']"
      :speed="20"
      scrollable
      :text="noticeText"
      :class="isSwiper ? 'swiper-notice-bar' : 'notice-bar'"
      @getmore="getMore"
    /> -->
    <uv-notice-bar
      :text="noticeText"
      :customStyle="customStyle"
      direction="column"
      :color="themeConfig['hubei-primary']"
      :more-color="themeConfig['hubei-primary']"
      :bgColor="themeConfig['hubei-primary-light']"
      mode="link"
      url="/pages-sub/notice/index"
      @click="handleClickNotice"
      @close="handleClickClose"
    ></uv-notice-bar>
  </view>
</template>

<script>
import { getWxNoticeRecommendList } from '@/api/home/index'
export default {
  name: 'noticebar',
  props: {
    dataConfig: {
      type: Object,
      default: () => {},
    },
    isSwiper: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      noticeText: [],
      noticeList: [],
      swiperList: [],
    }
  },
  computed: {
    themeConfig() {
      return this.$store.app.themeConfig
    },
    customStyle() {
      return {
        borderRadius: '100px',
      }
    },
  },
  methods: {
    getMore() {
      uni.navigateTo({
        url: '/pages-sub/notice/index',
      })
    },
    handleClickNotice(index) {
      console.log('点击了通知', index)
    },
    handleClickClose(index) {
      console.log('关闭了通知', index)
    },
    async getNoticeList() {
      let res = await getWxNoticeRecommendList({ mp_type: 'socialSports' })
      if (res.data.length > 0) {
        let text = res.data.map((e) => {
          return e.name
        })
        this.noticeList = res.data
        this.noticeText = text
        // this.noticeText = text.join('')
      }
    },
  },
  created() {
    this.getNoticeList()
  },
}
</script>

